.panel-show {
    margin-bottom: 20px;
    background-color: #fff;
    -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, .05);
    box-shadow: 0 1px 1px rgba(0, 0, 0, .05);
}

.panel-show .panel-body {
    padding: 1px;
}

.chart_fgp :active {
    cursor: move;
}

/* Chrome, Safari, Opera */
@-webkit-keyframes offline {
    from {
        color: green;
    }
    to {
        color: red;
    }
}

/* Standard syntax */
@keyframes offline {
    from {
        color: green;
    }
    to {
        color: red;
    }
}

/* Chrome, Safari, Opera */
@-webkit-keyframes online {
    from {
        color: red;
    }
    to {
        color: green;
    }
}

/* Standard syntax */
@keyframes online {
